import React, { Component } from 'react';
import axios from 'axios';


export default class searchCity extends Component {

constructor(props) {
super(props);
this.onChangeCity = this.onChangeCity.bind(this);

this.state = {
cityName: '',
cities: []
};
}

componentDidMount() {

axios.get('https://rate-my-city.herokuapp.com/listCities')
.then(response => {
if (response.data.length > 0) {
this.setState({
cities: response.data.map(cty => cty.name),
cityName: response.data[0].name
})
}
})
.catch(function (error) {
console.log(error);
});
}

onChangeCity(e) {
this.setState({
cityName: e.target.value
});
}

onSubmit(e) {
e.preventDefault();

const searchInfo = {
cityName: this.state.cityName
}

console.log(searchInfo);


this.setState ({
cityName: ''
})
}



render () {
return (
<div className="input-group mt-1 mb-2">
<form onSubmit={this.onSubmit}>
<div className="form-group">
<select
required
className="form-control"
value={this.state.cityName}
onChange={this.onChangeCity}>{
this.state.cities.map(function(cty) {
return <option
key={cty}
value={cty}>{cty}
</option>;
})
}
</select>
</div>

</form>


<input className="form-control" placeholder="Search by State" />
<input className="form-control" placeholder="Search by Username" />
<button className="btn text-white fw-bold btn-outline-dark btn-secondary" >Search</button>
</div>


)
}



};

